.App {
  text-align: center;
}

body {
  background-color: #1a1616;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #950101;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 3vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.vihrea{
    margin-right: 10px;
    border-radius: 4px;
    padding: 5px;
    padding-left: 8px;
    padding-right: 8px;
    border: none;
    font-family: 'Open Sans', sans-serif;
    transition-property: color, background, box-shadow;
    transition-duration: 0.2s;
    font-weight: 600;
    font-size: 0.8rem;
    box-shadow: 2px 2px rgb(64, 128, 0);
    background-color: chartreuse;
}
.vihrea:hover{
  background-color:rgb(64, 128, 0);
  color:white;
  box-shadow: 0px 0px rgb(64, 128, 0);
}

